@import "colors";


.home {
    display: flex;
    padding: 3rem;
    justify-content: center;
    flex-wrap: wrap;
}

.productcard {
    background-color: $tertiary;
    width: 300px;
    margin: 2rem;
    border-radius: 5px;
    padding: 2rem;
    transition: all 0.5s;
    display: flex;
    flex-direction: column;
    align-items: center;

    >img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        
       

    }

    >h4 {
        margin: 1rem;

    }

    >p{
        margin-top: 1rem;
    }

    >button {
        cursor: pointer;
        background-color: $secondary;
        border: none;
        padding: 1rem;
        color: black;
        border-radius: 5px;
        transition: all 0.5s;
        font-weight: 500;
        width: 100%;
        & :hover{
            background-color: $secondarydark;
        }
        


    }
    & :hover{
        transform: scale(1.05);
    }


}